<template>
   <div class="BxLogins">
      <div class="BxLogins__BxHeader">
          <img src="../../assets/img/dynedlivelogo.svg" height="50" alt="">
      </div>
      <div class="BxLogins__BxContent">
         <div class="BxLogins__BxContent--BxLottie">
            <PlanetCommetAstronotLayangan  v-if="ResizeScreen == 3"/>
            <PlanetCommet v-if="ResizeScreen == 1 || ResizeScreen == 2"/>
         </div>
         <div class="BxLogins__BxContent--BxLogins">
            <div class="bxInput">
               <v-snackbar
                  v-model="snackbar"
                  :color="NotifColor"
                  :timeout="timeout"
                  top
                  style="margin:auto;"
                  v-if="checkError"
                  >
                     <p style="margin-bottom:0px;text-align:center;width: 100%;">{{NotifText}}</p>
               </v-snackbar>
               <v-form ref="formlogin" v-model="valid" lazy-validation>
                  <v-text-field
                     v-model="email"
                     :rules="emailRules"
                     label="Email"
                     required
                     class="initest"
                     ></v-text-field>
                  <v-text-field
                     v-model="password"
                     :rules="[rules.required, rules.min]"
                     :type="show1 ? 'text' : 'password'"
                     name="input-10-1"
                     label="Password"
                     hint="At least 8 characters"
                     @click:append="show1 = !show1"
                     @keyup.enter="login"
            		  ></v-text-field>
                  <!-- <v-checkbox
                     v-model="checkbox"
                     label="Remember me"
                     required
                  ></v-checkbox> -->
                  
                  <v-layout class="BxFbtn">
                     <!-- <p class="v-label fpassword" :disabled="dialog" @click="dialog = true">Forgot Your password?</p> -->
                     <v-btn :disabled="!valid" class="btnRound liveBtn text-capitalize" @click="login">
                        Login
                     </v-btn>
                  </v-layout>
                  <p class="v-label fpassword" :disabled="dialog" @click="dialog = true">forgot your password?</p>
               </v-form>
            </div>
            <div class="bxUs" align-center justify-space-between row>
               <div>
                  <span> <router-link to="/about">About us</router-link></span>
               </div>
               <div>
                  <span><router-link to="/contact">Contact us</router-link></span>
               </div>
            </div>
            <v-dialog v-model="dialog" hide-overlay width="100" persistent  content-class="DGLoding" >
         <v-card class="fpassword--loder">
            <v-card-text class="text-xs-center">
               <v-progress-circular
                  indeterminate
                  color="cyan darken-1"
                  :size="50"
                  :width="10"
               ></v-progress-circular>
            </v-card-text>
         </v-card>
      </v-dialog>
      <v-dialog v-model="dialogLogin" content-class="DialogLoder" width="300" hide-overlay persistent style="display:flex;justify-content:center;">
         <v-card class="fpassword--loder">
            <v-card-text class="text-xs-center">
               <LittleLoading  style="height:250px;width:250px;overflow:hidden;"/>
            </v-card-text>
         </v-card>
      </v-dialog>
      <v-dialog v-model="dialog2" width="400" content-class="DGfpassword" persistent>
         <v-card style="border-radius: 27px;">
            <v-card-title class="headline bxhead">
               <v-btn outline color="red" @click.native="dialog2 = false"><v-icon>close</v-icon></v-btn>
               <p>Forgot Password</p>
            </v-card-title>
            <v-card-text>
               <div class="bx__forgotpassword">
                  <v-form ref="formchange" v-model="valid2" lazy-validation>
                     <p style="font-size:0.9em;text-align:center;margin-bottom: auto;">We just need your registered email address to send you password reset.</p>
                     <br>
                     <v-text-field
                        v-model="emailConfirm"
                        :rules="emailRules"
                        label="Your E-mail"
                        required
                        class="initest"
                     ></v-text-field>
                     <br>
                     <v-card-actions style="justify-content:center;">
                        <v-btn color="primary" @click="changepassword" :disabled="!valid2" class="text-capitalize">
                           Submit
                        </v-btn>
                     </v-card-actions>
                  </v-form>
               </div>
            </v-card-text>
        </v-card>
      </v-dialog>
       <v-dialog v-model="dialogLoding" width="150" persistent content-class="PersonalLoading">
         <v-card>
            <v-card-text class="text-xs-center">
               <v-progress-circular
                  indeterminate
                  color="primary"
                   :size="50"
                  :width="10"
               ></v-progress-circular>
            </v-card-text>
         </v-card>
      </v-dialog>
         </div>
      </div>
   </div>
</template>

<script>
   import { mapState } from 'vuex'
   import ICHelp from '../../components/icons/RoundHelp.vue'
   import PlanetCommet from '../Lotte/PlanetCommet'
   import PlanetCommetAstronotLayangan from '../Lotte/PlanetCommetAstronotLayangan'
   import LittleLoading from '../Lotte/LittleLoading'

   export default{
      components:{
        ICHelp,
        PlanetCommet,
        PlanetCommetAstronotLayangan,
        LittleLoading
      },
      data () {
  			return {
            valid: false,
            valid2: false,
            email: '',
            emailConfirm: '',
               emailRules: [
                  v => !!v || 'E-mail is required',
                  v => /.+@.+/.test(v) || 'E-mail must be valid'
               ],
               checkbox: false,
            show1: false,
            password: '',
            rules: {
               required: value => !!value || 'Required.',
               min: v => v.length >= 8 || 'Min 8 characters',
               emailMatch: () => ('The email or password you entered don\'t match'),
               passMatch: (v) => v == this.newpassword || 'New Password and Confirm Password must match'
            },
            dialog: false,
            dialog2: false,
            snackbar: false,
            timeout: 6000,
            NotifColor:'',
            NotifText:'',
            dialogLoding: false,
            windowWidth:0,
            windowHeigt:0,
            ResizeScreen: null,
            dialogLogin:false
  			}
      },
      created() {
         this.getWindowHeight()
         this.getWindowWidth()
      },
      mounted() {
         this.$store.dispatch("CLEAR_STATE");
         this.$nextTick( () => {
            window.addEventListener('resize', this.getWindowWidth);
            window.addEventListener('resize', this.getWindowHeight);
            this.getWindowHeight()
            this.getWindowWidth()
         })
      },
     
      computed:{
         checkError () {
            return this.showAlert(), this.$store.state.Error401
         },
         CheckForgotPS(){
            return this.$store.state.ForgotPass
         },
         CheckError2(){
            return this.$store.state.Message
         },
      },
      methods:{
         login () {
            if (this.$refs.formlogin.validate()) {
               const formLogin ={
                  username: this.email,
                  password: this.password,
                  // returnSecureToken : true
               }
               const ConvertFormLogin = JSON.stringify(formLogin)
               this.$store.dispatch('POST_LOGIN', ConvertFormLogin)
               this.dialogLogin = true
            }
         },
         changepassword () {
            if (this.$refs.formchange.validate()) {
               const DataForgot = {
                  email : this.emailConfirm
               }
               this.$store.dispatch('POST_FORGOT_PASSWORD', DataForgot)
            }
         },
         showAlert () {
            if (this.$store.state.Error401){
               this.valid=false
               this.dialogLogin = false
               this.NotifColor = 'error'
               this.NotifText = 'The email or password you entered don\'t match'
               this.snackbar = true
               setTimeout(() => (this.$store.state.Error401 = false, this.valid=true,this.snackbar = false), 4000)
            }
         },
         getWindowWidth(event){
            this.windowWidth = document.documentElement.clientWidth;
            //console.log(this.ResizeScreen)
            if (this.windowWidth <= 599){
            //this for resolution phone only
            this.ResizeScreen = 1
            }else if(this.windowWidth <= 768){
               //this for resolution Ipad Potrait
            this.ResizeScreen = 2
            }else if(this.windowWidth >= 1024){
               //this for resolution Dekstop-up
            this.ResizeScreen = 3
            }
         },
         getWindowHeight(event){
            this.windowHeigt = document.documentElement.clientHeight;
         }
      },

       watch: {
         CheckError2(val){
            if (val === 'login Fail'){
               this.dialogLogin = false
            }
         },
         dialog (val) {
            if (!val) return
            setTimeout(() => (this.dialog2 = true, this.dialog = false), 1000)
         },
         CheckForgotPS (val) {
            if (!val) return
            if(val.data.status === '200 OK'){
                  this.NotifColor ='success'
                  this.NotifText = 'We send the password reset link to your Email'
                  this.dialogLoding = true
                  setTimeout(()=>{
                     this.dialogLoding = false
                     this.snackbar = true
                     this.$refs.formchange.reset()
                     this.dialog2 = false
                  },2000);
            }else if(val.data === 400){
                  this.NotifColor ='error'
                  this.NotifText = "CAN'T FIND YOUR ACCOUNT"
                  this.dialogLoding = true
                  setTimeout(()=>{
                     this.dialogLoding = false
                     this.snackbar = true
                  },2000);
            }else if(val.data.email[0] === "validation.email"){
                  this.NotifColor ='error'
                  this.NotifText = 'EMAIL FORMAT IS WRONG'
                  this.dialogLoding = true
                  setTimeout(()=>{
                     this.dialogLoding = false
                     this.snackbar = true
                  },2000);
            }
         }
      },

      beforeDestroy() {
         window.removeEventListener('resize', this.getWindowWidth);
         window.removeEventListener('resize', this.getWindowHeight);
      },
   }
</script>

<style lang="scss">
    @import './Login.scss';
</style>


